@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*{padding:0 auto;margin:0 auto;box-sizing:border-box;font-family:"Raleway", sans-serif;color:#323232}.app{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100vh;padding:16px}.app__heading{font-family:"Lobster", cursive;font-size:42px;margin:20px 0;letter-spacing:8px;text-align:center}.app__colorContainer{display:flex;justify-content:center;align-items:center;margin:20px 0}.app__color{border-radius:5px;outline:none;margin:0 16px;width:80px;height:40px}.app .colorpick-eyedropper-input-trigger{display:none}.app__colorText{font-size:20px;margin:20px 0;letter-spacing:4px;text-align:center}.app__buttonContainer{display:flex;justify-content:space-between;align-items:center;margin:20px 0}.app__button{margin:0 10px;padding:5px 10px;border-radius:10px;outline:none;font-size:18px;cursor:pointer}@media (min-width: 768px){.app{padding:32px}.app__heading{font-size:56px}.app__colorText{font-size:28px}.app__button{font-size:20px;padding:10px 20px}}@media (min-width: 1200px){.app__heading{font-size:48px}.app__colorText{font-size:24px}.app__button{font-size:18px}}

